.star {
  display: flex;
  height: 15px;
  gap: 1px;

  @include vp-767 {
    height: 12px;
    gap: 2px;
  }

  span {
    display: block;
    background-image: url("../img/icons/star.svg");
    width: 15px;
    height: 100%;
    background-size: cover;
    background-repeat: no-repeat;
    margin: 0;
    padding: 0;

    &.is-active {
      background-image: url("../img/icons/star-active.svg");
    }

    @include vp-767 {
      width: 13px;
      height: 12px;
    }
  }
}
