.process__wrapper {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 30px;
  position: relative;
}
@media (max-width: 1199px) {
  .process__wrapper {
    grid-template-columns: repeat(2, 1fr);
    column-gap: 30px;
    row-gap: 82px;
  }
  .process__wrapper::after {
    position: absolute;
    content: "";
    width: calc(52% + 2px);
    height: 56px;
    background-color: transparent;
    border: 1px solid rgba(2, 102, 193, 0.2);
    border-left: 1px solid transparent;
    top: 47%;
    transform: translateY(-50%);
    left: 7px;
  }
}
@media (max-width: 767px) {
  .process__wrapper {
    grid-template-columns: repeat(1, 1fr);
    column-gap: 0;
    row-gap: 17px;
    padding-left: 37px;
  }
  .process__wrapper::after {
    position: absolute;
    content: "";
    width: 1px;
    height: 80%;
    background-color: rgba(2, 102, 193, 0.2);
    border: 0;
    top: 50%;
    left: 10px;
    transform: translateY(-50%);
  }
}

