  display: flex;
  align-items: center;
  gap: 15px;
  margin-bottom: 32px;
  position: relative;
}
@media (max-width: 1199px) {
  .process__item {
    margin-bottom: 8px;
  }
  .process__item:last-child {
    order: 3;
  }
  .process__item:nth-last-child(2) {
    order: 4;
  }
  .process__item:first-child {
    order: 1;
  }
  .process__item:nth-child(2) {
    order: 2;
  }
  .process__item:nth-last-child(-n+2)::after {
    top: -27px;
    bottom: auto;
  }
}
